html,
body,
.container {
  height: 100%;
  margin: 0;
}

body {
  background: #323c41;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
	height:100%;
	width:100%;
}

a {
  text-decoration: none;
  color: #ffffff;
}
.main {
	width: 100%;
	height: 100%;
}
nav {
	position: absolute;
	right: 0;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	padding: 20px;
}
.nav-item {
	text-decoration: none;
	color: white;
	letter-spacing: .1em;
	margin: 0 0 0 1em;
}

.nav-item:hover {
	color: #c4ac6f;
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: #fff;
  background: url(/static/media/bg.b4604c43.jpg) no-repeat center center fixed;
  background-size: cover;
}

.container h1 {
  font-family: 'josefin sans';
  font-size: 60px;
  font-weight: 700;
  letter-spacing: .3em;
  text-transform: uppercase;
  color: #c4ac6f;
}

.typingContainer {
  margin: 0 auto;
}

.linkContainer {
  position: absolute;
  bottom: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.linkContainer img {
  width: 25px;
  margin: 25px;
  text-align: center;
}


#wrap {
  background: url(/static/media/project.cedc00aa.jpg) no-repeat center center;
  background-size: cover;
  width: 100%;
  height: 30%;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
}

#wrap h1 {
  align-self: center;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: #fff;
}

#lightings {
  width: 100%;
}

section {
  width: 100%;
}

#one {
  /*animation*/
  -webkit-animation: one 5s ease-in-out infinite alternate;
  animation: one 5s ease-in-out infinite alternate;
}

@-webkit-keyframes one {
  from {
    -webkit-box-shadow: 0 0 250px 20px #473C78;
  }
  to {
    -webkit-box-shadow: 0 0 100px 15px #F72A3B;
  }
}

@keyframes one {
  from {
    box-shadow: 0 0 250px 20px #473C78;
  }
  to {
    box-shadow: 0 0 100px 15px #F72A3B;
  }
}

#two {
  width: 90%;
  /*animation*/
  -webkit-animation: two 4s ease-in-out infinite alternate;
  animation: two 4s ease-in-out infinite alternate;
}

@-webkit-keyframes two {
  from {
    -webkit-box-shadow: 0 0 250px 20px #18C499;
  }
  to {
    -webkit-box-shadow: 0 0 100px 15px #D8F05E;
  }
}

@keyframes two {
  from {
    box-shadow: 0 0 250px 20px #18C499;
  }
  to {
    box-shadow: 0 0 100px 15px #D8F05E;
  }
}

#three {
  width: 80%;
  /*animation*/
  -webkit-animation: three 3s ease-in-out infinite alternate;
  animation: three 3s ease-in-out infinite alternate;
}

@-webkit-keyframes three {
  from {
    -webkit-box-shadow: 0 0 250px 20px #FFDD00;
  }
  to {
    -webkit-box-shadow: 0 0 100px 15px #3E33FF;
  }
}

@keyframes three {
  from {
    box-shadow: 0 0 250px 20px #FFDD00;
  }
  to {
    box-shadow: 0 0 100px 15px #3E33FF
  }
}

#four {
  width: 70%;
  /*animation*/
  -webkit-animation: four 2s ease-in-out infinite alternate;
  animation: four 2s ease-in-out infinite alternate;
}

@-webkit-keyframes four {
  from {
    -webkit-box-shadow: 0 0 250px 20px #781848;
  }
  to {
    -webkit-box-shadow: 0 0 100px 15px #F2BBE9;
  }
}

@keyframes four {
  from {
    shadow: 0 0 250px 20px #781848;
  }
  to {
    shadow: 0 0 100px 15px #F2BBE9;
  }
}

#five {
  width: 60%;
  /*animation*/
  -webkit-animation: five 1s ease-in-out infinite alternate;
  animation: five 1s ease-in-out infinite alternate;
}

@-webkit-keyframes five {
  from {
    -webkit-box-shadow: 0 0 250px 20px #42F2A1;
  }
  to {
    -webkit-box-shadow: 0 0 100px 15px #F4F6AD;
  }
}

@keyframes five {
  from {
    box-shadow: 0 0 250px 20px #42F2A1;
  }
  to {
    box-shadow: 0 0 100px 15px #F4F6AD;
  }
}
.project {
	width: 100%;
	height: 60%;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	color: #fff;
}

.project img {
	width: 30%;
}

.project p {
	max-width: 50%;
	font-size: 20px;
	padding-bottom: 20px;
}

.project a {
	padding: 15px 40px 15px 40px;
    font-size: 20px;
}

.project a:hover {
	color: #fff;
    -webkit-filter: brightness(70%);
    transition: all 1s ease;
}
.card-container {
    display: grid;
    padding: 1rem;
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    grid-gap: 1rem;
}
.blog-header {
  width: 100%;
  height: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url(/assets/blog.jpg) no-repeat center/cover fixed;
  background-size: cover;
}

.blog-header h1 {
  font-family: 'josefin sans';
  font-size: 60px;
  font-weight: 700;
  letter-spacing: .3em;
  text-transform: uppercase;
  color: #c4ac6f;
}
.blog-card {
  position: relative;
  display: inline-flex;
  justify-content: space-between;
  height: 500px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 3px 3px 20px rgba(0, 0, 0, 0.5);
}

.gradient-overlay {
  background-image: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.6) 21%);
  position: absolute;
  width:100%;
  height: 30%;
  top: 70%;
}

.color-overlay {
  background: rgba(84, 104, 110, 0.4);
  transition: background 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);
  position: absolute;
  width:100%;
  height: 100%;
}

.blog-card:hover .color-overlay {
  background: rgba(84, 104, 110, 0.8);
}

.card-content {
  display: flex;
  flex-grow: 1;
  z-index: 1;
  flex-direction: column;
  justify-content: space-between;
}

.title-content h3 {
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  color: #9CC9E3;
  font-family: 'Roboto', sans-serif;
  text-align: center;
}

.title-content hr {
  width: 50px;
  height: 3px;
  margin: 20px auto;
  border: 0;
  background: #D0BB57;
}

.introduction {
  color: #DCE3E7;
  font-family: 'Droid Serif', serif;
  font-size: 13px;
  font-style: italic;
  line-height: 18px;
  text-align: center;
}

.card-info {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  margin: 0 auto;
  padding: 10px 50px;
  color: #DCE3E7;
  font-family: 'Droid Serif', serif;
  line-height: 24px;
  z-index: 2;
  opacity: 0;
  transition: bottom 0.3s, opacity 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);
}

.blog-card:hover .card-info {
  opacity: 1;
}

.card-button {
  background: transparent;
  outline: none !important;
  line-height: 42px;
  padding: 0;
  border: solid 2px;
  text-align: center;
  margin-top: 150px;
}

.card-button:hover {
  color: #c4ac6f;
}


.utility-list {
  list-style-type: none;
  margin: 0 0 30px 20px;
  padding: 0;
  width: 100%;
}
.utility-list li {
  margin: 0 15px 0 0;
  padding: 0 0 0 22px;
  display: inline-block;
  color: #DCE3E7;
  font-family: 'Roboto', sans-serif;
}
.utility-list li.time {
  background: url(/static/media/time.016b2fbf.svg) no-repeat;
}
.utility-list li.date {
  background: url(/static/media/calendar.9b0617e2.svg) no-repeat;
}
.post {
	padding-top: 100px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 auto;
	width: 70%;
}

.post h1 {
  font-family: 'josefin sans';
  font-size: 60px;
  font-weight: 700;
  color: #c4ac6f;
}

.post img {
    max-width: 70%;
    height: auto;
    width: auto\9; /* ie8 */
    padding-bottom: 50px;
}

.post .content {
	color: #fff;
	width: auto;
}
